<template>
  <div>
    <button id="topButton" @click="addABubble">发 射</button>
    <!--<div class="bubbleClass">-->
      <!--<div></div>-->
      <!--<div></div>-->
      <!--<div></div>-->
      <!--<div></div>-->
    <!--</div>-->
  </div>

</template>

<script>
export default {
  name: 'HelloWorld',
  data :function() {
    return {
      msg: 'Welcome to Your Vue.js App',
      bubbleArr:[]
    }
  },
  methods:{
      addABubble:function(){
        function Bubble(){
          this.div = document.createElement("div");
          this.div.className = "bubbleClass";
          this.div.innerHTML = "<div></div><div></div><div></div><div></div>";
          app.appendChild(this.div);
          var divArr = this.div.childNodes;
          divArr[0].style.opacity = Math.random();
          divArr[1].style.opacity = Math.random();
          divArr[2].style.opacity = Math.random();
          var symbol1 = 1.1;//符号
          var symbol2 = 1.1;
          var symbol3 = 1.1;
//          for(let i=0;i<3;i++){
//            setInterval(function(){
//              divArr[i].style.opacity = divArr[i].style.opacity*symbol;
//              if(divArr[i].style.opacity>=1){
//                symbol = 0.9;
//              }else if(divArr[i].style.opacity <= 0.05){
//                symbol = 1.1;
//              }
//            },100);
//          }
          setInterval(function(){
            divArr[0].style.opacity = divArr[0].style.opacity*symbol1;
            if(divArr[0].style.opacity>=1){
              symbol1 = 0.9;
            }else if(divArr[0].style.opacity <= 0.05){
              symbol1 = 1.1;
            }
            divArr[1].style.opacity = divArr[1].style.opacity*symbol2;
            if(divArr[1].style.opacity>=1){
              symbol2 = 0.9;
            }else if(divArr[1].style.opacity <= 0.05){
              symbol2 = 1.1;
            }
            divArr[2].style.opacity = divArr[2].style.opacity*symbol3;
            if(divArr[2].style.opacity>=1){
              symbol3 = 0.9;
            }else if(divArr[2].style.opacity <= 0.05){
              symbol3 = 1.1;
            }
          },200);
          this.div.style.top = "30px";
          this.div.style.left = "30px";
          var speedX = 200*Math.random() ;//100px * 随机数 每秒
          var speedY = 200*Math.random() ;//100px * 随机数 每秒
          var screenX = window.innerWidth;
          var srceenY = window.innerHeight;
         var time1 = () =>{
            var top =   parseFloat(this.div.style.top);
            var left =   parseFloat(this.div.style.left);
            this.div.style.top = top + speedY/10+ "px" ;
            this.div.style.left = left + speedX/10+ "px" ;
            if(parseFloat(this.div.style.top)>=window.innerHeight- 222){
              speedY = -speedY;
            }else if(parseFloat(this.div.style.top)<=0){
              speedY = -speedY;
            }
            if(parseFloat(this.div.style.left)>=window.innerWidth - 222){
              speedX = -speedX;
            }else if(parseFloat(this.div.style.left)<=0){
              speedX = -speedX;
            }

          }
          setInterval(time1,30);
        }
        var bubble = new Bubble();
        this.bubbleArr.push(bubble);
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
  @size:222px;
  .bubbleClass{
    width:@size;
    height:@size;
    position:absolute;
    top:30px;
      >div{
          width:@size;
          height:@size;
          position:absolute;

         /*background:url('../assets/images/red.png')*/
      }
         >div:nth-child(1){
          background:url('../assets/images/red.png')
         }
        >div:nth-child(2){
          background:url('../assets/images/green.png')
        }
        >div:nth-child(3){
           background:url('../assets/images/blue.png')
         }
        >div:nth-child(4){
           background:url('../assets/images/background.png')
         }
  }
  #topButton{
    padding:10px 20px;
    border-radius:3px;
    border:0 solid #888;
    margin:0 auto;
  }
  #topButton:hover{
    cursor:pointer;
  }
</style>



